<template>
  <div class="withdraw-box">
    <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>Your Package Activation History</span>
            </div>
          </template>
          <el-table :data="tableData" border style="width: 100%">
    <el-table-column prop="date" label="Package Name" width="180"> </el-table-column>
    <el-table-column prop="name" label="Price" width="180"> </el-table-column>
    <el-table-column prop="address" label="Amount Paid"> </el-table-column>
     <el-table-column prop="address" label="Activation Date"> </el-table-column>
  </el-table>
        </el-card>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import { useI18n } from "vue-i18n"
import {
  getGasPrice,
  getHashrate,
  getBlockNumber,
  getBlockInfo,
  getBlockList,
  getTransactionList
} from "@/api";
export default {
  components: {},
  setup() {
    const router = useRouter();
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      tableData: [
          {
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄',
          },
          {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄',
          },
          {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄',
          },
          {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄',
          },
        ],
      toBlockDetail: (row, column, event) => {
        router.push({
          name: "BlockDetial",
          params: { blockNumber: row.number }
        });
      },
      toTransactionDetail: (row, column, event) => {
        router.push({ name: "TransactionDetial", params: { hash: row.hash } });
      }
    });
    onMounted(() => {
      getGasPrice().then(res => {
        data.gasPrice = res.data;
      });
      getHashrate().then(res => {
        data.hashrate = res.data;
      });
      getBlockNumber()
        .then(res => {
          data.blockNumber = res.data;
          return getBlockInfo({ blockNumber: res.data });
        })
        .then(block => {
          data.difficulty = block.data.difficulty;
        });
      getBlockList().then(res => {
        data.blockList = res.data;
      });
      getTransactionList().then(res => {
        data.transactionList = res.data;
      });
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss" scoped>
.card-header {
  text-align: left;
  font-size: 18px;
  font-weight: bold;
}
</style>
