<template>
  <div class="withdraw-box">
    <el-card class="box-card">
          <template #header>
            <div class="card-header flex-between">
              <span>会员列表</span> 
              <el-form :inline="true" class="demo-form-inline">
    <el-form-item label="">
      <el-input v-model="searchUser" style="width: 400px" placeholder="输入用户地址"></el-input>
    </el-form-item>
   
    <el-form-item>
      <el-button type="primary" @click="onSearch">搜索</el-button>
      <el-button type="primary" @click="onClear">清除</el-button>
    </el-form-item>
  </el-form>
            </div>
          </template>
          <el-table :data="list" border style="width: 100%">
    <el-table-column prop="user" label="用户地址"> </el-table-column>
    <el-table-column prop="intro" label="推荐人"> </el-table-column>
    <el-table-column width="150" label="投资数量">
       <template #default="scope">
           {{Number(scope.row.investAmount/10**18).toFixed(2)}}
          </template>
    </el-table-column>
    <el-table-column width="150" label="运营总业绩">
      <template #default="scope">
           {{Number(scope.row.performance/10**18).toFixed(2)}}
          </template>
   </el-table-column>
    <el-table-column width="150" label="运营收益"> 
      <template #default="scope">
           {{Number(scope.row.shareAmount/10**18).toFixed(2)}}
          </template>
    </el-table-column>
    <el-table-column width="150" label="已提现数量">
      <template #default="scope">
           {{Number(scope.row.withdrawAmount/10**18).toFixed(2)}}
          </template>
       </el-table-column>
    <el-table-column width="180" label="创建时间"> 
     <template #default="scope">
           {{dateFormat("yyyy-MM-dd hh:mm:ss",new Date(scope.row.time * 1000 || 0))}}
          </template>
    </el-table-column>
    <el-table-column fixed="right" label="查看" width="80">
      <template #default="scope">
        <el-button type="text" size="small" @click="toDetail(scope.row.user)"
          >查看详情</el-button
        >
      </template>
    </el-table-column>
  </el-table>
        </el-card>
  
  </div>
    <el-pagination
       style="margin-top: 30px"
        background
        layout="prev, pager, next"
        @current-change="currentChange"
        :current-page="Number(page)"
        :page-count="count"
      >
      </el-pagination>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRouter, useRoute, onBeforeRouteUpdate } from "vue-router";
import apolloProvider from '@/utils/apollo';
import gql from "graphql-tag";

export default {
  components: {},
  setup() {
    const router = useRouter();
    const route = useRoute()
    const data = reactive({
      list: [],
      limit: 12,
      page: route.query.page || 1,
      count: 1,
      searchUser: '',
      getList: () => {
        data.loading = true
        apolloProvider.clients.swapClient.query({
          query: gql`query userLists($first: Int!,$skip: Int!) {
            userLists(first: $first, skip: $skip, orderBy: time, orderDirection: desc) {
              id
              user
              intro
              investAmount
              performance
              shareAmount
              withdrawAmount
              time
            }
          }`,
          variables: {
            first: data.limit,
            skip: (data.page - 1) * data.limit
          },
          fetchPolicy: 'no-cache'
        }).then( (res) => {
          let list = res.data.userLists
          if (list.length <= 0) {
            return
          }
           if( list.length < data.limit) {
          } else {
            data.count = Number(data.page) + 1
            console.log(data.count)
          }
          data.list = list
        })
      },
      onSearch: () => {
         data.getSearchList()
      },
      getSearchList: () => {
        data.page = 1
        data.count = 1
        apolloProvider.clients.swapClient.query({
          query: gql`query userLists($first: Int!,$skip: Int!, $user: Bytes!) {
            userLists(first: $first, skip: $skip, orderBy: time, orderDirection: desc, where: {user: $user}) {
              id
              user
              intro
              investAmount
              performance
              shareAmount
              withdrawAmount
              time
            }
          }`,
          variables: {
            skip: 0,
            first: 1,
            user: data.searchUser
          },
          fetchPolicy: 'no-cache'
        }).then( (res) => {
          let list = res.data.userLists
          data.list = list
        })
      },
      onClear: () => {
        this.searchUser = ''
        this.getList()
      },
      currentChange: number => {
        router.replace({ name: "User", query: { page: number } });
      },
      toDetail: (address) => {
        router.push({ name: "UserDetail", params: { address: address } });
      }
    });
    onMounted(() => {
      data.getList()
    });
     onBeforeRouteUpdate((to, from) => {
      data.page = to.query.page || 1;
      data.getList()
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss" scoped>
.card-header {
  text-align: left;
  font-size: 18px;
  font-weight: bold;
}
</style>
