<template>
  <div class="withdraw-box">
    <el-card class="box-card" style="margin-bottom: 20px">
      <el-descriptions border title="我的投资数据">
    <el-descriptions-item label="地址">{{userInfo.user}}</el-descriptions-item>
    
     <el-descriptions-item label="投资数量">
    {{Number(userInfo.investAmount/10**18).toFixed(2)}}
    </el-descriptions-item>
    <el-descriptions-item label="运营总业绩">{{Number(userInfo.performance/10**18).toFixed(2)}}</el-descriptions-item>
    <el-descriptions-item label="运营收益">
    {{Number(userInfo.shareAmount/10**18).toFixed(2)}}
    </el-descriptions-item>
    
    <el-descriptions-item label="已提现数量"
      >
      {{Number(userInfo.withdrawAmount/10**18).toFixed(2)}}
      </el-descriptions-item
    >
      <el-descriptions-item label="创建时间"
      >
        {{dateFormat("yyyy-MM-dd hh:mm:ss",new Date(userInfo.time * 1000 || 0))}}
      </el-descriptions-item
    >
    <el-descriptions-item label="推荐人">
      <router-link class="text-ellipsis" :to="{name: 'UserDetail', params: { address: userInfo.intro }}"> {{userInfo.intro}}</router-link>
     </el-descriptions-item>
  </el-descriptions>
  </el-card>
    <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>我的直推</span>
            </div>
          </template>
          <el-table :data="list" border style="width: 100%">
    <el-table-column prop="user" label="用户地址"> </el-table-column>
    <el-table-column prop="intro" label="推荐人"> </el-table-column>
    <el-table-column width="150" label="投资数量">
       <template #default="scope">
           {{Number(scope.row.investAmount/10**18).toFixed(2)}}
          </template>
    </el-table-column>
    <el-table-column width="150" label="运营总业绩">
      <template #default="scope">
           {{Number(scope.row.performance/10**18).toFixed(2)}}
          </template>
   </el-table-column>
    <el-table-column width="150" label="运营收益"> 
      <template #default="scope">
           {{Number(scope.row.shareAmount/10**18).toFixed(2)}}
          </template>
    </el-table-column>
    <el-table-column width="150" label="已提现数量">
      <template #default="scope">
           {{Number(scope.row.withdrawAmount/10**18).toFixed(2)}}
          </template>
       </el-table-column>
    <el-table-column width="200" label="创建时间"> 
     <template #default="scope">
           {{dateFormat("yyyy-MM-dd hh:mm:ss",new Date(scope.row.time * 1000 || 0))}}
          </template>
    </el-table-column>
      <el-table-column fixed="right" label="查看" width="80">
      <template #default="scope">
        <el-button type="text" size="small" @click="toDetail(scope.row.user)"
          >查看详情</el-button
        >
      </template>
    </el-table-column>
  </el-table>
        </el-card>
  
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRouter, useRoute, onBeforeRouteUpdate } from "vue-router";
import apolloProvider from '@/utils/apollo';
import gql from "graphql-tag";

export default {
  components: {},
  setup() {
    const router = useRouter();
    const route = useRoute()
    const data = reactive({
      list: [],
      limit: 1000,
      userInfo: {},
      address: route.params.address,
      getList: () => {
        data.loading = true
        apolloProvider.clients.swapClient.query({
          query: gql`query userLists($first: Int!,$skip: Int!, $intro: Bytes!) {
            userLists(first: $first, skip: $skip, orderBy: time, orderDirection: desc, where: {intro: $intro}) {
              id
              user
              intro
              investAmount
              performance
              shareAmount
              withdrawAmount
              time
            }
          }`,
          variables: {
            skip: 0,
            first: data.limit,
            intro: data.address
          },
          fetchPolicy: 'no-cache'
        }).then( (res) => {
          let list = res.data.userLists
          data.list = list
        })
      },
      getUserInfo: () => {
        data.loading = true
        apolloProvider.clients.swapClient.query({
          query: gql`query userLists($first: Int!,$skip: Int!, $user: Bytes!) {
            userLists(first: $first, skip: $skip, orderBy: time, orderDirection: desc, where: {user: $user}) {
              id
              user
              intro
              investAmount
              performance
              shareAmount
              withdrawAmount
              time
            }
          }`,
          variables: {
            skip: 0,
            first: 1,
            user: data.address
          },
          fetchPolicy: 'no-cache'
        }).then( (res) => {
          let list = res.data.userLists
          data.userInfo = list[0]
        })
      },
      toDetail: (address) => {
        router.replace({ name: "UserDetail", params: { address: address } });
      }
    });
    onMounted(() => {
      data.getList()
      data.getUserInfo()
    });
     onBeforeRouteUpdate((to, from) => {
      data.address = to.params.address;
      data.getList()
      data.getUserInfo()
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss" scoped>
.card-header {
  text-align: left;
  font-size: 18px;
  font-weight: bold;
}
</style>
